body{
    background-color: #b61924;
}


div.transition-group {
    position: relative;
}

.fade-enter {

    opacity: 0.01;
  }

  .fade-enter.fade-enter-active {


    opacity: 1;
    -webkit-transition: opacity 500ms ease-in-out;
    transition: opacity 500ms ease-in-out;
  }

  .fade-exit {
    opacity: 1;
  }

  .fade-exit.fade-exit-active {

    opacity: 0.01;
    -webkit-transition: opacity 500ms ease-in-out;
    transition: opacity 500ms ease-in-out;
  }





